import React, { Component } from "react";
import GridRow from "./GridRow";
import { bindComponentToDataSetState } from "../../redux/reducers/dataReducer";
import { gridColumns } from "./gridColumns";

class GridBody extends Component {
  render() {
    const {
      data,
      dataSource,
      dataSourceName,
      showSelectColumn,
      reverse,
    } = this.props;
    const rows = reverse ? data.rows.reverse() : data.rows;
    const columns = gridColumns(this.props.columns, this.props.dataSource.columns);
    return (
      <tbody>
        {rows.map((row) => (
          <GridRow
            key={row.position}
            position={row.position}
            dataSource={dataSource}
            dataSourceName={dataSourceName}
            showSelectColumn={showSelectColumn}
            row={row}
            columns={columns}
          />
        ))}
      </tbody>
    );
  }
}

export default bindComponentToDataSetState(GridBody);
